.fullScreen {
    position: absolute;
    height: 100%;
    width:100%;
    background-color: black;
}

div.fullScreen {
    background-color: black;
    color: #eee;
}

.sharp {
    image-rendering: pixelated;
    image-rendering: -moz-crisp-edges;
    object-fit: contain;
}

body {
    margin: 0;
}